}

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px;
}

.h1 {
  font-size: 48px;
  line-height: 58px;
  font-weight: 700;
  text-transform: uppercase;
  color: #3E9CDC;
}
.h1 span {
  color: #000000;
}

.h2 {
  font-size: 36px;
  line-height: 43px;
  font-weight: 800;
  text-transform: uppercase;
  color: #ffffff;
}
.h2 span {
  color: #3E9CDC;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-transform: uppercase;
}
.btn_primary {
  color: #ffffff;
  background-color: #3E9CDC;
  border: 1px solid #ffffff;
  border-radius: 4px;
  transition: all 0.25s;
}
@media screen and (min-width: 767px) {
  .btn_primary:hover {
    color: #ffffff;
    background-color: #286690;
    border-color: #ffffff;
  }
}
.btn_secondary {
  color: #2A2B30;
  background-color: #ffffff;
  border: 1px solid #6F6D7B;
  border-radius: 4px;
  transition: all 0.25s;
}
@media screen and (min-width: 767px) {
  .btn_secondary:hover {
    color: #ffffff;
    background-color: #286690;
    border-color: #ffffff;
  }
}

.form__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.form__item {
  position: relative;
  width: 100%;
  height: 48px;
}
.form__item_two {
  grid-column: span 2;
}
.form__input {
  width: 100%;
}
.form__input_text {
  height: 100%;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 3px;
  border: 2px solid #ffffff;
  padding: 12px 10px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  color: #272733;
  transition: all 0.25s;
}
.form__input_text:focus {
  border: 2px solid #3E9CDC;
  background: #ffffff;
}
.form__input_range {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
}
.form__input_file {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
}
.form__label {
  position: absolute;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
}
.form__label_text {
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: #272733;
  transition: all 0.25s;
}
.form__label_text.focus {
  left: 0;
  top: -26px;
  transform: translateY(0);
  color: #ffffff;
}
.form__label_range {
  top: 0;
  left: 0;
  color: #ffffff;
}
.form__label_file {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border: 2px solid #3E9CDC;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.25s;
}
@media screen and (min-width: 767px) {
  .form__label_file:hover {
    color: #ffffff;
    background-color: #3E9CDC;
    border-color: #ffffff;
  }
}
.form__label_file img {
  margin-right: 8px;
}
.form__label_file span {
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  color: #272733;
  text-transform: uppercase;
}
.form__file {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  background-color: #3E9CDC;
  border: 2px solid #ffffff;
  border-radius: 3px;
  color: #ffffff;
  z-index: 2;
  padding: 12px 10px;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}
.form__file-name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.form__file-remove {
  padding-left: 8px;
  color: #000000;
  cursor: pointer;
}
.form__file.visible {
  display: flex;
}
.form__output {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  color: #ffffff;
}
.form__submit {
  height: 48px;
  width: 100%;
  max-width: 372px;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  margin: 55px auto 0;
}

input[type=range] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
  border-radius: 10px/100%;
  height: 6px;
  border: 1px solid #ffffff;
  background: rgba(255, 255, 255, 0.7);
}

input[type=range]::-webkit-slider-thumb {
  background: #42A9ED;
  border: 3px solid #286690;
  border-radius: 50%;
  cursor: pointer;
  width: 26px;
  height: 26px;
  -webkit-appearance: none;
  margin-top: -10px;
}

input[type=range]::-moz-range-track {
  border-radius: 10px/100%;
  height: 4px;
  border: 1px solid #ffffff;
  background: rgba(255, 255, 255, 0.7);
  transform: translateY(6px);
}

input[type=range]::-moz-range-thumb {
  background: #42A9ED;
  border: 3px solid #286690;
  border-radius: 50%;
  cursor: pointer;
  width: 22px;
  height: 22px;
  transform: translateY(6px);
}

