    line-height: 32px;
  }
  .h2 {
    font-size: 18px;
    line-height: 24px;
  }
  .form__body {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  .form__item:nth-of-type(1), .form__item:nth-of-type(4) {
    order: 1;
  }
}
@media screen and (max-width: 576px) {
  .form__body {
    grid-template-columns: repeat(1, 1fr);
  }
  .form__item:nth-of-type(1), .form__item:nth-of-type(4) {
    order: 0;
  }
  .form__item_two {
    grid-column: span 1;
  }
  .form__submit {
    max-width: none;
    margin: 20px 0 0;
  }
  .form__label_range {
    font-size: 13px;
    line-height: 16px;
  }
  .form__label_text.focus {
    top: -18px;
    font-size: 12px;
    line-height: 16px;
  }
}
.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  color: #272733;
  width: 100%;
  height: 100%;
}
.select__hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}
.select__styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.85);
  border: 1px solid #ffffff;
  border-radius: 3px;
  padding: 12px 10px;
  transition: all 0.25s ease-in;
}
.select__styled:after {
  content: url(../img/down.svg);
  position: absolute;
  top: 8px;
  right: 16px;
}
.select__styled:hover {
  background-color: #ffffff;
}
.select__styled:active, .select__styled.active {
  background: rgba(61, 64, 80, 0.85);
  color: #ffffff;
  border: 1px solid rgba(61, 64, 80, 0.85);
  border-radius: 3px 3px 0 0;
}
.select__styled:active:after, .select__styled.active:after {
  content: url(../img/up.svg);
}
.select__options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  background: #282834;
  border: 1px solid rgba(61, 64, 80, 0.85);
  border-radius: 0 0 3px 3px;
}
.select__options li {
  margin: 0;
  padding: 12px 0;
  text-indent: 15px;
  transition: all 0.25s ease-in;
  color: #ffffff;
}
.select__options li:hover, .select__options li.is-selected {
  color: #3E9CDC;
}
.select__options li[rel=hide] {
  display: none;
}

@media screen and (max-width: 1240px) {
  .select {
    font-size: 14px;
    line-height: 20px;
  }
  .select__styled {
    padding: 9px 10px;
  }
}
